import React, { Component } from "react";

export default class Cookies extends Component {
  render() {
    return (
      <div className="Cookies">
        <div className="container-cookies">
          <div className="cookies-circle blank">!</div>
          <div className="cookies-text">
            <p>
              Nasz strona internetowa używa plików cookies(tzw. ciasteczka) w
              celach statystycznych, reklamowych oraz funkcjonalnych. Dzięki nim
              możemy indywidualnie dostosować stronę do twoich potrzeb. Każdy
              może zaakceptowac pliki cookies albo ma możliwość wyłaczenia ich w
              przeglądarce, dzięki czemu nie będą zbierane żadne informacje.
            </p>
          </div>
        </div>
      </div>
    );
  }
}



